@font-face {
   font-family: 'Roboto';
   src: url("../../assets/fonts/roboto/Roboto-Bold.ttf") format('truetype');
   font-weight: bold;
   font-style: normal;
}

@font-face {
   font-family: 'Roboto-Italic';
   src: url("../../assets/fonts/roboto/Roboto-Italic.ttf") format('truetype');
   font-weight: italic;
   font-style: normal;
}

:root {
   --primary-font: 'Roboto', sans-serif;
   --secondary-font: 'Roboto-Italic'
}

body {
   color: #e8e8e8;
   font-family: var(--primary-font);
   overflow: hidden;
}

#particles {
   position: absolute;
   z-index: -1;
}

#dev_message {
   border-radius: 15px;
   border-style: groove;
   border-color: #9644bb;
   width: fit-content;
   padding: 10px;
   position: absolute;
   top: 0;
}

.scroll-container {
   scroll-snap-type: y mandatory;
   /* Vertical scroll-snapping */
   overflow-y: scroll;
   /* Enable vertical scrolling */
   height: 100vh;
   /* Full viewport height */
}

.hero-container,
.skills-container,
.jobs-container {
   scroll-snap-align: start;
   /* Snap sections to the top */
   height: 100vh;
   /* Full viewport height for snapping */
   display: flex;
   justify-content: center;
   align-items: center;
}


.hero-container {
   position: relative;
   display: flex;
   margin-inline: 20vh;
   margin-top: 10vh;
   scroll-snap-type: y mandatory;
}

.hero_intro {
   font-family: var(--primary-font);
   height: fit-content;
   width: auto;
   margin-top: 10%;
   margin-left: 10%;
}

.hero_intro h2 {
   font-size: 30px;
}

.hero_intro h3 {
   opacity: 70%;
   color: rgb(154, 154, 154);
}

.hero_content h2 span:first-child {
   display: block;
}

.hero_content h2 {
   font-family: var(--primary-font);
   font-weight: bold;
   width: 100%;
   height: auto;
   font-size: 34px;
}

.hero_image {
   width: 340px;
   height: auto;
   border-radius: 10px;
}

.hero_intro .msgBtn {
   font-family: var(--primary-font);
   padding: 6px 20px;
   font-size: 15px;
   font-weight: 0;
   border-radius: 6px;
   transition: background-color 0.5s;
}

.msgBtn {
   margin-right: 20px;
}

.msgBtn:hover {
   background-color: #0e1212;
   border-color: #d8d8d8;
   border-width: 0.5px;
   color: #e8e8e8;
   border-radius: 6px;
   cursor: pointer;
}

.button-link {
   font: bold 11px Arial;
   text-decoration: none;
   background-color: #EEEEEE;
   color: #333333;
   padding: 2px 6px 2px 6px;
   border-top: 1px solid #CCCCCC;
   border-right: 1px solid #333333;
   border-bottom: 1px solid #333333;
   border-left: 1px solid #CCCCCC;
}

.skills-container {
   display: flex;
   flex-direction: column;
   align-items: center;
   margin-top: 20vh;
   z-index: 2;
   position: relative;
}

.skills-title {
   font-size: 4vh;
   text-align: center;
   margin-bottom: 0;
   margin-top: 20vh;
}

.skills {
   scroll-snap-type: y mandatory;
   background-color: rgb(37, 38, 38);
   display: flex;
   font-size: 17px;
   margin-top: 20px;
   margin-inline: 10%;
   margin-bottom: 20%;
}

.skills h2 {
   padding-inline: 20px;
   cursor: pointer;
   width: fit-content;
}

.skills p {
   background-color: #333333;
   padding: 10px;
   width: 50%;
   position: absolute;
   left: 25%;
   transition: transform 0.6s ease-in-out;
}

.jobs-container {
   margin-bottom: 30vh;
   margin-top: 20vh;
   display: flex;
   flex-direction: column;
}

.jobs-title {
   font-size: 4vh;
   text-align: center;
}

.jobs {
   display: flex;
   gap: 30px;
}

.jobs h2 {
   font-family: var(--secondary-font);
   background-color: #333333;
   margin-inline: auto;
   border-style: groove;
   border-radius: 15px;
   padding: 7vh;
}



@media only screen and (device-width: 390px) and (device-height: 844px) and (-webkit-device-pixel-ratio: 3) and (orientation: portrait) {

   .scroll-container {
      scroll-snap-type: none; /* Disable snapping */
      overflow-y: auto;       /* Still allow scrolling */
   }
   
   .hero-container,
   .skills-container,
   .jobs-container {
      scroll-snap-align: none; /* Remove individual snapping */
   }
   
   .hero-container {
      flex-direction: column;
      margin-inline: 5vh;
      margin-top: 5vh;
   }

   .hero_intro {
      margin: 5% 0;
      width: 100%;
   }

   .hero_intro h2 {
      font-size: 22px;
   }

   .hero_intro h3 {
      font-size: 16px;
   }

   .hero_content h2 {
      font-size: 26px;
   }

   .hero_image {
      display: block;
      width: 70%;
      margin-inline: auto;
      height: auto;
      margin-top: 100%;
   }

   .hero_intro .msgBtn {
      font-size: 14px;
      padding: 8px 15px;
   }

   .button-container {
      display: flex;
      flex-direction: column;
      margin-top: 10vh;
   }

   .msgBtn {
      margin-inline: 20px;
      margin-top: 5vh;
      height: 8vh;
   }

   .skills-container {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-top: 7vh;
      z-index: 2;
      position: relative;
   }

   .skills {
      display: flex;
      flex-wrap: wrap;
      margin-inline: 5%;
      font-size: 15px;
   }

   .skills p {
      top: 75vh;
      font-size: 14px;
      left: 0;
      width: 100%;
   }

   .skills-title,
   .jobs-title {
      font-size: 24px;
   }

   .jobs-container {
   margin-bottom: 30vh;
   margin-top: 0;
   display: flex;
   flex-direction: column;
   height: auto;
}

   .jobs {
      flex-direction: column;
      align-items: center;
      gap: 20px;
   }

   .jobs h2 {
      font-size: 16px;
      padding: 5vh;
      text-align: center;
   }
}